@import '../library';
* {
    box-sizing: border-box;
    -webkit-overflow-scrolling: touch;
  }
  
  html {
    min-height: 100%;
    scroll-behavior: smooth;
    font-size: 62.5%;
  }
  
  body {
    color: $body-color;
    display: block;
    font-family: $font-family-montserrat;
    font-size: $font-base;
    font-weight: $font-weight;
    line-height: $line-height-base;
    margin: 0;
    padding: 0;
    height: 100%;
    
  }
  

  a {
      color: $primary;
      text-decoration: none;
  }

  figure {
    margin: 0;
  }

  hr {
    border: 0 none;
    border-top: 1px solid #ddd;
    margin: 10px 0 15px;
  }

  li {
      @extend %remove-space;
      list-style: none;
    }
    
    ul, ol {
      @extend %remove-space;
      list-style: none;
    }

#root {
    
  h1, h2 {
    &::before {
        content: '';
        width: 100px;
        display: inline-block;
        border-top: 2px solid #40D2E7;
        margin-bottom: 5px;
    }

    span {
        display: block;
    }
  }
}

.swal2-modal .swal2-styled:focus {
	-webkit-box-shadow: none;
	box-shadow: none;
}